import React from 'react';
import Pic from "../img/pizza1.jpg";
import Pic2 from "../img/img1.jpg";
import Pic3 from "../img/pizza.jpg";
function Gallery() {
    return (
        <>
            <div className="pics" id="gallery">
                    <div className="roll">
                        <h1> Gallery</h1>
                        <h3>THE PIZZA PICTURES</h3>
                    </div>
                <div className="picleft">
                    <img src={Pic} alt="pic1"/>
                 </div>
                <div className="picright">
                    <img src={Pic2} alt="pic2" />
                    <img src={Pic3} alt="pic3" id="dem"/>
                </div>
            </div>
        </>
    )}
export default Gallery;