import React,{useState} from 'react';

function Contect() {
    const [data, setData] = useState({
        fullname:"",
        phone:"",
        email:"",
        msg:"",
    });

   const changeEvent =(event)=>{
       const{name,value} = event.target;

       setData((preVal)=>{
           return{
               ...preVal,
               [name]:value,
           };
       });
   };
    
   const formSubmit = (e) => {
       e.preventDefault();
       alert(`My name is${data.fullname}.
My mobile number is ${data.phone},
my emaii id is ${data.email}.
Order is ${data.msg}`);
   };

    return (
        <div class="contents" id="order">        
            <div className="my-5">
                <h1 className="text-center">Order Now...</h1>
            </div>
            <div className="container contact_div">
                <div className="row">
                <div className="col-md-6 col-10 mx-auto">
    <form onSubmit={formSubmit}>
    <div class="form-group">
    <label for="exampleFormControlInput1">FullName</label>
    <input type="text" class="form-control" onChange={changeEvent}
    value={data.fullname} id="exampleFormControlInput1" name="fullname" placeholder="Fullname"/>

    <label for="exampleFormControlInput1">Email address</label>
    <input type="email" class="form-control"  onChange={changeEvent}  name="email" value={data.email} id="exampleFormControlInput1" placeholder="name@example.com"/>

    <label for="exampleFormControlInput1">Phone Number.</label>
    <input type="number" class="form-control"  onChange={changeEvent} name="phone" value={data.phone} id="exampleFormControlInput1" placeholder="Phone No."/>
  
  <div class="form-group">
    <label for="exampleFormControlTextarea1">Order</label>
    <textarea className="form-control" id="exampleFormControlTextarea1"  name="msg" onChange={changeEvent} value={data.msg} rows="3"></textarea>
        </div>
    <div className="col-12">
        <button className="btn btn-outline-primary"> Submit</button>
    </div>
    </div>
 </form>               
 </div>
         </div>
     </div>
 </div>
    )
}

export default Contect;
