import React from 'react'

function Item(props) {
    return (
      
          <div className="price">
                <div className="price__img">
                    <img src={props.pimg} alt="pizza"/>
                </div>
                    <h1 className="price__heading"> {props.heading } </h1>
                <p className="price__text">
                     {props.text}
                </p>
                <p className="price_rs">{props.price}</p>
            </div>
          
    )
}

export default Item;
