import React from 'react';
import Logo from "../img/logo.png"


const Navbar =()=> {
    return (
        <div>
          <nav className="navbar">
                  <div className="logo">
                    <img src={Logo}  alt="logo"/>
                  </div>
                  <div className="links">
                    <ul>
                      <li>
                          <a href="#menu">MENU</a>
                      </li>
                      <li>
                          <a href="#prices">PRICE</a>
                      </li>
                      <li>
                          <a href="#order">ORDER</a>
                     </li>
                     <li>
                          <a href="#gallery">GALLERY</a>
                     </li>
                     <li>
                          <a href="#shop">SHOP</a>
                     </li>
                    </ul> 
                    </div>    
               </nav>  
        </div>
    )
}

export default Navbar
