import React from "react";

const Shop = () => {
    return(
        <div className="shop" id="shop">
            <div className="container">
                <div className="shop_text">
                    <div>
                        <h1>Free Delivery With PIZZA OF THE DAY ONLY $8.99</h1>
                        <div className="shop__btn">
                            <a to="" className="btn btn-smart">
                            SHOP NOW
                            </a>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    )
}

export default Shop;